.snackbarRoot {
  width: 100%;

  transition: transform 105ms cubic-bezier(1, 1, 0, 0) 105ms,
  visibility 105ms cubic-bezier(0, 0, 1, 1) 105ms !important;

  div:only-child {
    height: 90px !important;
    min-width: 100% !important;
    max-width: 100% !important;
  }
}

.snackbarContainer {
  display: flex;
  align-items: center;
  height: 100%;
  font-size: 17pt;
  align-self: flex-end;
  line-height: 1.2;
}

.snackbarIcon {
  svg {
    vertical-align: middle;
    width: 42px !important;
    height: 42px !important;
  }
}

.snackbarMessage {
  width: 80%;
  margin-left: 20px;

  .mainMessage {
    color: #ffffff;
    word-wrap: break-word;
    display: block;
  }

  .subMessage {
    font-size: 15pt;
    opacity: 0.7;
  }
}
