.character-picker {
  position: absolute;
  bottom: 0;
  left: -16px;
  display: flex;
  align-items: center;
  height: 50px;
  font-size: 30px;
  background-color: #000000;
  outline: none;
  width: calc(100% + 16px);
  z-index: 1;
  overflow: auto;

  .character-picker-item-container {
    padding-left: 10px;
    padding-right: 10px;
    &.option {
      &.submit {
        margin-left: 50px;
        // margin-right: 20px;
        svg {
          color: #298a36 !important;
        }
      }
    }

    &.active {
      border-bottom: 3px solid #00aaed;
    }
  }
}