.input-dialog-field {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: center;
  width: 70%;
  .text-field {
    .text-field-label {
      font-size: 18px;
      color: rgb(0, 170, 237);
    }

    .text-field-value-container {
      border-bottom: 2px solid #00aaed;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      padding-top: 3px;
      padding-bottom: 3px;
      height: 35px;
      .text-field-value {
        height: 25px;
        display: flex;
        align-items: center;
        padding-right: 2px;
        border-right: 1px #ffffff solid;
        animation: blinking 1s infinite ease;
        &.default-value {
          background-color: #00aaed;
        }
      }
      .text-field-units {
        color: grey;
      }
    }

    .text-field-underline-text {
      font-size: 18px;
      color: grey;
    }
  }

  .text-field-container {
    display: flex;
    flex-direction: column;
    flex: 1;
  }

  .text-field-left-icon-container {
    align-items: center;
    display: flex;
    margin-right: 5px;

    svg {
      height: 17px !important;
      width: 17px !important;
      color: grey !important;
    }
  }

  &.error {
    .text-field-label {
      color: red;
    }

    .text-field-value-container {
      border-bottom: 2px solid red;
    }

    .text-field-underline-text {
      color: red;
    }
  }
}

@keyframes blinking {
  100% { border-right: 0; }
}
