.input-dialog-container {
  position: absolute;
  display: flex;
  justify-content: center;
  background-color: black;
  height: calc(100% - 16px);
  width: 100%;
  left: 0;
  top: 0;
  padding-top: 16px;

  .input-dialog-field-container {
    display: flex;
    flex: 1;
    align-items: center;
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    justify-content: center;
  }
  .input-dialog {
    display: flex;
    flex-direction: column;
    align-items: center;

    .input-dialog-title {
      font-size: large;
    }
  }
}
