.confirmation-dialog {
  display: flex;
  flex-direction: column;
  display: flex;
  flex: 1;
  height: 100%;

  .confirmation-dialog-container {
    display: flex;
    flex-direction: column;
    flex: 1;
  }

  .confirmation-dialog-header {
    display: flex;
    align-items: center;
    .confirmation-dialog-title {
      font-weight: bold;
    }
    .confirmation-dialog-icon {
      margin: 10px;
      svg {
        width: 50px !important;
        height: 50px !important;
      }
    }
  }

  .confirmation-dialog-body {
    display: flex;
    flex: 1;
  }

  .confirmation-dialog-options {
    display: flex;
    flex: 5;
    align-items: flex-end;
    padding-bottom: 25px;
    justify-content: space-around;

    .option-button {
      display: flex;
      height: 70px;
      justify-content: center;
      flex: 1;
      margin-right: 15px;
      margin-left: 15px;
      &.active {
        border-bottom: 6px solid #00aaed;

        .option-button-container {
          background-color: #535353;
        }
      }

      .option-button-container {
        align-items: center;
        justify-content: center;
        display: flex;
        flex: 1;
        margin-bottom: 5px;
        border-radius: 4px;
      }
    }
  }
}