.table-input-dialog-container {
  display: flex;
  flex-direction: column;
  flex: 1;

  .instructions {
    position: absolute;
    top: -13px;
    right: 16%;
    font-style: italic;
  }

  .table-input-dialog {
    display: flex;
    flex-direction: column;
    height: 100%;

    .table-input-dialog-header {
      font-weight: 600;
    }

    .table-input-dialog-body {
      flex: 1;
      overflow-y: auto;
    }
  
    .table-input-dialog-row {
      display: flex;
      justify-content: space-around;
      position: relative;
      &.active {
        background-color: #404040;
      }
      .table-input-dialog-row-active-mark {
        border-left: 6px solid #00aaed;
        position: absolute;
        height: 100%;
        left: 0;
      }
    }

    .table-input-dialog-cell {
      display: flex;
      flex: 2;
      justify-content: center;
      align-items: center;
      height: 45px;
      border: 1px solid grey;
      &.table-input-dialog-cell-number {
        flex: 0.5;
      }
    }
  }

  .input-dialog-title {
    font-size: large;
    color: #292929;
  }

  .input-dialog-instructions {
    color: #00aaed;
    font-size: large;
  }
}
